.picture {
    transition: filter 0.3s linear;
  }

.picture__container {
    position: relative;
    top: 0px;
    cursor: pointer;
}

.picture__tagMissing {
    position: absolute;
    top: 50%;
    text-align: center;
    width: 100%;
    font-weight: 200;
    opacity: .75;
    letter-spacing: 2px;
    margin-bottom: 10px;
    font-size: 0.7em;
}

.picture__tagShow {
    position: absolute;
    top: 42%;
    width: 100%;
    text-align: center;
    font-size: 1.5em;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s, opacity 0.3s linear;
}

.picture__bluryGray{
    filter: grayscale(100%) brightness(25%);
}

.picture__id{    
    font-weight: 200;
    font-size: .25em;
    letter-spacing: 2px;
    margin-top: 1em;
}