.overlay__overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1;
}

.overlay__background {
  background-color: rgba(0, 0, 0, 0.75);
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  z-index: 0;
}

.overlay__columnRight {
  right: 0;
  height: 100vh;
  z-index: 2;
}

.overlay__columnLeft {
  left: 0;
  height: 100vh;
  z-index: 2;
}

.overlay__columnLeft,
.overlay__columnRight {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  width: 10%;
  font-size: 2em;
  cursor: pointer;
}

.overlay__columnLeft:hover,
.overlay__columnRight:hover {
  background-color: rgba(255, 255, 255, 0.15);
}

.overlay__closeButton {
  position: fixed;
  right: 0;
  top: 0;
  width: 10%;
  height: 2.5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 2em;
  cursor: pointer;
  z-index: 3;
  visibility: visible;
  opacity: 1;
}

.overlay__closeButton:hover {
  background-color: rgba(107, 12, 5, 0.5);
}

.overlay__pictureContainer {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 0px;
  user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none;
}

.overlay__pictureHover {
  position: absolute;
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none;
  cursor: pointer;
}

.overlay__pictureWatermark {
  text-shadow: 5px 5px 2px rgba(0, 0, 0, 0.05);
  color: rgba(255, 255, 255, 0.05);
  font-size: 3em;
  font-weight: 500;
  transform: rotate(-45deg);
  user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none;
}

.overlay__picture {
  max-width: 80vw;
  max-height: 80vh;
  display: block;
  user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none;
}

.overlay__infoAction {
  width: 100%;
  font-weight: 200;
  letter-spacing: 2px;
  font-size: .6em;
  padding-bottom: .3em;
}

.overlay__info {
  width: 15%;
  padding-left: .3em;
  display: inline-block;
  text-align: left;
}

.overlay__action {
  width: 85%;
  padding-right: .3em;
  display: inline-block;
  text-align: right;
  opacity: .7;
}

.overlay__tags{
  width: 100%;
  font-weight: 200;
  letter-spacing: 2px;
  font-size: .6em;
  opacity: .7;
  text-align: left;
  padding-top: .3em;
}

.overlay__tags span{
  color: rgba(255, 255, 255, 0.5);
  background-color: rgba(0, 0, 0, 0.5);
  padding: .1em .2em .2em .2em;
  cursor: pointer;
}

.overlay__tags span:hover{
  color: rgba(255, 255, 255, 1);
}

.overlay__heart{
  position: absolute;
  font-size: 1em;
  opacity: 0;
  visibility: hidden;
  transition: font-size 500ms, visibility 0s linear 0s, opacity 500ms;
}

.overlay__spinner {
  color: #FFF;
  z-index: 10;  
  font-size: 10vh;
}